* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: rgba(216, 168, 10, 0.789);
    padding: 20px 80px;
}

#navul {
    display: flex;
    align-items: center;
    justify-content: center;
}

#navul li {
    list-style-type: none;
    padding: 0 20px;
    position: relative;
}

#navul li a {
    text-decoration: none;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: 500;
}

#navul li a:hover,
#navul li a.home {
    color: rgba(253, 220, 120, 0.979);
}

#navul li a:hover::after,
#navul li a.home::after {
    content: "";
    width: 55%;
    height: 3px;
    background: rgba(98, 168, 238, 0.333);
    position: absolute;
    bottom: -4px;
    left: 20px;
}

.icon {
    display: none;
}

.icon i {
    color: rgba(1, 67, 250, 0.911);
}

@media screen and (max-width:769px) {
    #navul {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        position: fixed;
        top: 90px;
        right: -300px;
        width: 300px;
        height: 100vh;
        background: #ff9090;
        padding: 40px 0 0 10px;
        transition: 0.3s ease-in-out;
    }
    #navul.active{
        right: 0px;
    }

    #navul li {
        margin-bottom: 20px;
    }

    .icon {
        display: block;
    }

    .icon i {
        font-size: 24px;
    }

}